import Vue from 'vue'
import VueRouter from 'vue-router'
import Index from '../views/index.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/login',
    name: 'login',
    component: () => import('@/views/login.vue')
  },
  {
    path: '/',
    name: 'Index',
    component: Index,
    redirect: '/index',
    meta: { requiresAuth: true },
    children: [
      {
        path: "/index",
        name: 'userinfo',
        redirect: '/userinfo/alluser',
        component: () => import('@/components/userinfo.vue'),
        children: [
          {
            path: "/userinfo/adduser",
            name: 'adduser',
            component: () => import('@/components/userinfo/adduser.vue')
          }, {
            path: "/userinfo/alluser",
            name: "alluser",
            component: () => import('@/components/userinfo/alluser.vue'),

          }, {
            path: "/userinfo/jsz",
            name: "alluser",
            component: () => import('@/components/userinfo/jsz.vue'),
          }
        ]
      }, {
        path: "/userinfo/details",
        name: "details",
        component: () => import('@/components/userinfo/details.vue'),
      }
    ],
  },
  // {
  //   path: '/MIndex', // 手机端首页
  //   name: "MIndex",
  //   component: () => import('@/Moblie/MIndex.vue')
  // },
]


const router = new VueRouter({
  mode: 'history',
  routes
})

router.beforeEach((to, from, next) => {
  if (to.matched.some(record => record.meta.requiresAuth)) {    //判断是否需要登
    if (localStorage.isLogin) {
      next()
    } else {

      console.log(to.path)
      next({
        path: '/login',
        query: { target: to.path }
      }
      );
    }
  } else {
    next()
  }
})
export default router
